import React, { useState } from "react";
import Button from "@mui/material/Button";
import "../Calculator/Calculator.css";
const Calculator = () => {
  const [rezult, setRezult] = useState("");
  const handleClick = (e) => {
    setRezult(rezult.concat(e.target.name));
  };
  const clear = () => {
    setRezult("");
  };
  const backspace = () => {
    setRezult(rezult.slice(0, rezult.length - 1));
  };
  const calculate = () => {
    try {
      setRezult(eval(rezult).toString());
    } catch (err) {
      setRezult("error");
    }
  };
  return (
    <div className="buttons">
      <div>
        <input
          type="text"
          id="outlined-basic"
          variant="outlined"
          value={rezult}
        />
      </div>

      <Button variant="contained" name="-" onClick={handleClick}>
        -
      </Button>
      <Button variant="contained" name="/" onClick={handleClick}>
        /
      </Button>

      <Button variant="contained" name="*" onClick={handleClick}>
        *
      </Button>
      <Button variant="contained" name="+" onClick={handleClick}>
        +
      </Button>
      <br />
      <Button variant="contained" name="6" onClick={handleClick}>
        6
      </Button>
      <Button variant="contained" name="7" onClick={handleClick}>
        7
      </Button>
      <Button variant="contained" name="8" onClick={handleClick}>
        8
      </Button>
      <Button variant="contained" name="9" onClick={handleClick}>
        9
      </Button>
      <br />
      <Button variant="contained" name="2" onClick={handleClick}>
        2
      </Button>

      <Button variant="contained" name="3" onClick={handleClick}>
        3
      </Button>
      <Button variant="contained" name="4" onClick={handleClick}>
        4
      </Button>
      <Button variant="contained" name="5" onClick={handleClick}>
        5
      </Button>

      <br />
      <Button variant="contained" name="1" onClick={handleClick}>
        1
      </Button>
      <Button variant="contained" name="0" onClick={handleClick}>
        0
      </Button>
      <Button variant="contained" name="="  color="success"  onClick={calculate}>
        =
      </Button>
      <Button variant="contained" name="." onClick={handleClick}>
        .
      </Button>
      <br />
      <Button variant="contained" name="CLear" id="clear"  color="success" onClick={clear}>
        CLear
      </Button>
      <Button variant="contained" name="C" id="backspace"  color="success" onClick={backspace}>
        C
      </Button>
    </div>
  );
};

export default Calculator;
